import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AutomotiveManufacturingComponent } from './automotive-manufacturing/automotive-manufacturing.component';
import { BankingFinanceComponent } from './banking-finance/banking-finance.component';
import { EntertainTelecomComponent } from './entertain-telecom/entertain-telecom.component';
import { HealthcarePayerComponent } from './healthcare-payer/healthcare-payer.component';
import { HealthcareProviderComponent } from './healthcare-provider/healthcare-provider.component';
import { HospitalityRetailComponent } from './hospitality-retail/hospitality-retail.component';
import { HigherEducationComponent } from './higher-education/higher-education.component';
import { InsuranceServiceComponent } from './insurance-service/insurance-service.component';
import { IndustriesComponent } from './industries.component';


const routes: Routes = [
  {
    path: '',
    component: IndustriesComponent,
    children: [
      { path: 'automotive-manufacturing', component: AutomotiveManufacturingComponent },
      { path: 'banking-finance', component: BankingFinanceComponent },
      { path: 'entertain-telecom', component: EntertainTelecomComponent },
      { path: 'healthcare-payer', component: HealthcarePayerComponent },
      { path: 'healthcare-provider', component: HealthcareProviderComponent },
      { path: 'hospitality-retail', component: HospitalityRetailComponent },
      { path: 'higer-education', component: HigherEducationComponent },
      { path: 'insurance-service', component: InsuranceServiceComponent }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class IndustriesRoutingModule { }
